import { generateUrl } from '@nextcloud/router';
import { encodeFilePath } from '../utils/fileUtils';
import { getToken, isPublic } from '../utils/davUtils'; // preview width generation

var previewWidth = 256;
export default {
  name: 'TemplatePreview',
  inheritAttrs: false,
  props: {
    basename: {
      type: String,
      required: true
    },
    checked: {
      type: Boolean,
      default: false
    },
    fileid: {
      type: [String, Number],
      required: true
    },
    filename: {
      type: String,
      required: true
    },
    previewUrl: {
      type: String,
      default: null
    },
    hasPreview: {
      type: Boolean,
      default: true
    },
    mime: {
      type: String,
      required: true
    },
    ratio: {
      type: Number,
      default: null
    }
  },
  data: function data() {
    return {
      failedPreview: false
    };
  },
  computed: {
    /**
     * Strip away extension from name
     *
     * @return {string}
     */
    nameWithoutExt: function nameWithoutExt() {
      return this.basename.indexOf('.') > -1 ? this.basename.split('.').slice(0, -1).join('.') : this.basename;
    },
    id: function id() {
      return "template-picker-".concat(this.fileid);
    },
    realPreviewUrl: function realPreviewUrl() {
      // If original preview failed, fallback to mime icon
      if (this.failedPreview && this.mimeIcon) {
        return this.mimeIcon;
      }

      if (this.previewUrl) {
        return this.previewUrl;
      } // TODO: find a nicer standard way of doing this?


      if (isPublic()) {
        return generateUrl("/apps/files_sharing/publicpreview/".concat(getToken(), "?fileId=").concat(this.fileid, "&file=").concat(encodeFilePath(this.filename), "&x=").concat(previewWidth, "&y=").concat(previewWidth, "&a=1"));
      }

      return generateUrl("/core/preview?fileId=".concat(this.fileid, "&x=").concat(previewWidth, "&y=").concat(previewWidth, "&a=1"));
    },
    mimeIcon: function mimeIcon() {
      return OC.MimeType.getIconUrl(this.mime);
    }
  },
  methods: {
    onCheck: function onCheck() {
      this.$emit('check', this.fileid);
    },
    onFailure: function onFailure() {
      this.failedPreview = true;
    }
  }
};