import { subscribe } from '@nextcloud/event-bus';

(function () {
  var FilesPlugin = {
    attach: function attach(fileList) {
      var _this = this;

      subscribe('nextcloud:unified-search.search', function (_ref) {
        var query = _ref.query;
        fileList.setFilter(query);
      });
      subscribe('nextcloud:unified-search.reset', function () {
        _this.query = null;
        fileList.setFilter('');
      });
    }
  };
  window.OC.Plugins.register('OCA.Files.FileList', FilesPlugin);
})();